.modal {
  z-index: 30;

  &__overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 30;

    background: rgba(48, 53, 65, 0.9);
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &-side {
    .modal__overlay {
      justify-content: flex-end;
      align-items: stretch;
    }

    .modal__container {
      max-height: 100vh;
    }

    &.modal-side_big {
      .modal__container {
        width: 1185px;
      }

      &#modal-registration {
        @media (--max-tablet) {
          .modal__container {
            max-width: unset;
            width: 100vw;
          }
        }
      }
    }

    &.modal-side_bigger {
      .modal__container {
        max-width: unset;
        width: 87.5vw;

        @media (max-width: 420px) {
          width: 100vw;
        }
      }
    }
  }

  &_almost-full-screen {
    .modal__container {
      max-width: 1185px;
      height: 95vh;
      width: 95vw;
    }
  }

  &__container {
    background: #fff;
    width: 790px;
    max-width: 85vw;
    max-height: 90vh;
    overflow-y: auto;

    scrollbar-width: thin;
    scrollbar-color: var(--blue-900) var(--purple-300);

    @media (max-width: 370px) {
      max-width: unset;
      width: 100vw;
    }

    &::-webkit-scrollbar {
      width: 7px;
    }

    &::-webkit-scrollbar-track {
      background: var(--blue-900);
    }

    &::-webkit-scrollbar-thumb {
      background-color: var(--purple-300);
      border: 0px solid var(--blue-900);
    }
  }
}

/**************************\
  Demo Animation Style
\**************************/
@keyframes mmfadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes mmfadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes mmslideIn {
  from {
    transform: translateX(15%);
  }
  to {
    transform: translateY(0);
  }
}

@keyframes mmslideOut {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateX(10%);
  }
}

.micromodal-slide {
  display: none;
}

.micromodal-slide.is-open {
  display: block;
}

.micromodal-slide[aria-hidden='false'] .modal__overlay {
  animation: mmfadeIn 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide[aria-hidden='false'] .modal__container {
  animation: mmslideIn 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide[aria-hidden='true'] .modal__overlay {
  animation: mmfadeOut 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide[aria-hidden='true'] .modal__container {
  animation: mmslideOut 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide .modal__container,
.micromodal-slide .modal__overlay {
  will-change: transform;
}

.micromodal-appear {
  display: none;
}

.micromodal-appear.is-open {
  display: block;
}

.micromodal-appear[aria-hidden='false'] .modal__overlay {
  animation: mmfadeIn 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-appear[aria-hidden='false'] .modal__container {
  animation: mmfadeIn 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-appear[aria-hidden='true'] .modal__overlay {
  animation: mmfadeOut 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-appear[aria-hidden='true'] .modal__container {
  animation: mmfadeOut 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-appear .modal__container,
.micromodal-appear .modal__overlay {
  will-change: transform;
}
